.clients-chat {
  &__title-h4 {
    margin-top: 69px;
    margin-bottom: 45px;
    display: flex;
    justify-content: center;
    font-style: normal;
    font-weight: 900;
    font-size: 36px;
    line-height: 100px;
    color: #0B2A37;
  }

  &__posts {
    margin-left: 35px;
    margin-right: 35px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    .post {
      width: 410px;
      height: 219px;
      margin-bottom: 40px;
      background: url(../img/clients-chat/rectangular.png) no-repeat;
      background-size: contain;

      &__comment {
        margin-left: 22px;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        color: #0B2A37;
      }

      &__autor {
        margin-top: 70px;
        margin-left: 231px;
        display: flex;

        .autor-info {
          display: flex;
          flex-direction: column;

          &__name {
            width: max-content;
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 26px;
            align-self: flex-end;
          }

          &__role {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 26px;
          }
        }

        &-avatar {
          width: 50px;
          height: 50px;
          background: url(../img/clients-chat/aneta.png) no-repeat;
          border-radius: 50%;
          background-size: cover;
          background-position-x: center;

          &--katarzyna {
            background: url(../img/clients-chat/katarzyna.png) no-repeat;
            background-position-x: center;
            background-size: cover;
          }
        }
      }
    }
  }
}

@media (max-width: 900px) {
  .clients-chat {

    &__title-h4 {
      margin-top: 40px;
      margin-bottom: 30px;
      font-size: 30px;
      line-height: 50px;
    }

    &__posts {

      .post {
        width: 500px;
      }
    }
  }
}
